import locandina from '../assets/locandina_ornella.jpg';

const Events = () => {

    return (

        <div className="poster-container">
        <img src={locandina} alt="Locandina" className="poster-image" />
        <h1 className="poster-title">Data e luogo di apertura</h1>
        <p className="poster-date">11 Aprile 2025</p>
        <p className="poster-location">Arezzo, Toscana</p>
      </div>
    );


}

export default Events;