import { useState } from "react";
import { useEffect } from "react";
import photo from "../assets/image0.png"

const useIsMobile = () => {
  const [isMobile, setIsMobile] = useState(window.innerWidth < 1024);

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth < 1024);
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  return isMobile;
};
const ProfilePhotoCover = () => {
  const isMobile = useIsMobile();



  return (
    
    <div
      style={{
        bottom: isMobile ? '80%' : '70%',
        right: isMobile ? '0vh' : '0vh',
        width: "256px",
        height: "256px",
        position: "absolute",
      }}
    >
            <style>{`
        @keyframes slide {
          to {
            transform: translateY(0);
            opacity: 1;
          }
        }
           @keyframes popIn {
          0% {
            opacity: 0;
            transform: scale(0.5);
          }
          80% {
            opacity: 1;
            transform: scale(1.1);
          }
          100% {
            opacity: 1;
            transform: scale(1);
          }
        }
      `
      
      
      }
      
      </style>
      {/* <span
        style={{
          
          position: "absolute",
          width: "140px",
          height: "32px",
          left: "120px",
          top: "120px",
          fontFamily: "Comfortaa",
          fontStyle: "normal",
          fontWeight: "700",
          fontSize: "18px",
          lineHeight: "180%",
          color: "#25282B",
          display: 'inline-block',
          opacity: 0,
          transform: "scale(0.5)",
          animation: 'popIn 1s ease-out 1s forwards',
        }}
      >
        Ornella Xhemalaj
      </span> */}
      
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          padding: "20px",
          gap: "48px",
          width: "256px",
          height: "300px",
          alignItems: 'center',
          backgroundImage: `url(${photo})`,
          borderRadius: '20px 5px 5px 20px',
          transform: 'translateX(100%)',
          opacity: 0,
          animation: 'slide 2s ease-out forwards',
          boxShadow: '0 4px 8px rgba(0, 0, 0, 0.3)',
          backgroundRepeat: "no-repeat"
        }}
      >
        <span style={{
          fontSize: '14px',
          fontFamily: "'Tree Ghost', sans-serif"
                                                
        }}>Ornella Xhemalaj, classe 1999.
Sono una creativa con esperienza in disegno digitale, grafica,
 design e creazione di oggetti illustrati. Mi occupo anche di pittura ad acrilico, 
acquerello e tecniche miste, inclusi collage con elementi naturali, lana e creta.
La mia ricerca artistica si concentra principalmente sull'illustrazione e la letteratura 
per l'infanzia. Tra le opere che maggiormente influenzano il mio lavoro ci sono Matilda di
 Roald Dahl, Coraline di Neil Gaiman e Il Giardino Segreto di Frances Hodgson Burnett, che ispirano 
 il mio approccio creativo, in particolare per l'ambientazione, le storie e le emozioni che trasmettono.
 </span>
      </div>
    </div>
    
  );
};

export default ProfilePhotoCover;
